import React from 'react'
import logo from'./assets/logo.png'
import { Link } from 'react-router-dom'
import './navbar.css'

function Navbar() {
  function burgerMenu(){
    const mobileMenu = document.querySelector(".mobileMenu")
    mobileMenu.classList.toggle("show");
  }
  return (
    <>
      <nav className='navbarm'>
        <ul>
            <li><Link to="/">   </Link></li>
            <li><a href="/#reverts-scroll">   </a></li>
            <li><Link to="/freequraan">   </Link></li>
            <li><Link to="/"><img alt="Find The truth" src={logo}/></Link></li>
            <li><Link to="/donate">  </Link></li>
            <li><Link to="/contact">   </Link></li>
            <li><Link to="/login">   </Link></li>
        </ul>
        
      </nav>
      <nav className='navbarmm'>
      <ul>
          <li><a href="/"><img alt="Find The truth" src={logo}/></a></li>
          <li><button onClick={burgerMenu}><i class="fa-solid fa-bars"></i></button></li>
          <div className="mobileMenu">
          <ul>
            <li><a className='mobileMenuItem' href="/">   </a></li>
            <li><a className='mobileMenuItem' href="/#reverts-scroll">   </a></li>
            <li><a className='mobileMenuItem' href="//freequraan">   </a></li>
            <li><a className='mobileMenuItem' href="/donate">  </a></li>
            <li><a className='mobileMenuItem' href="/contact">   </a></li>
            <li><a className='mobileMenuItem' href="/login">   </a></li>
            
        </ul>
        </div>
      </ul>
      
    </nav>
    </>
  )
}

export default Navbar
